<style lang="less" scoped>
@import "./admintrstor-assessment.less";
</style>

<template>
  <div class="bg_ff" style="margin-bottom: 10px;">
    <div>
      <Row class="app-resource-group-title">
        <h3>
        </h3>
      </Row>
      <Row v-show="isAdminTrue" class="assessmentForm pad15" >
          <Form 
            ref="formValidate" 
            :label-width="150" 
            :model="adminAssessData" 
            :rules="ruleValidate">
            <FormItem label=":" prop="duringDate">
              <DatePicker 
                format="yyyy-MM" 
                type="month" 
                placeholder="" 
                style="width: 200px" 
                v-model="adminAssessData.duringDate">
              </DatePicker>
            </FormItem>
            <FormItem label=":" prop="result" style="margin-bottom: 65px;">
              <vue-wangeditor 
                ref="result" 
                id="editorResult" 
                :menus="menu" 
                height="143" 
                width="100%"
                 class="editor-result">
                 </vue-wangeditor>
            </FormItem>
            <FormItem label=":" prop="opportunity" style="margin-bottom: 40px;">
              <vue-wangeditor 
                ref="oppor" id="editorOppor" 
                :menus="menu" 
                height="143" 
                width="100%">
                </vue-wangeditor>
            </FormItem>
          </Form>
          <div style="text-align:right;margin-bottom:5px;">
            <Button 
              @click="submitAdminAssess" 
              class="radius0" 
              style="background-color: rgb(0, 150, 136) !important;color:#fff;margin-top:5px"></Button>
          </div>
      </Row>
     
      <div v-for="(assess, index) of assessments" :key="index" class="pad10 bg_ff assessmentItem">
        <img :src="assess.photo?assess.photo:'resources/images/icon/contactor.png'" class="user-icon">
        <div class="font14 content">
          <div>
            <h4>{{ assess.creator }}
              <span class="fr">{{ formatDate(assess.date) }}</span>
            </h4>
          </div>
          <div class="assessment-info">
            <div>
              <span style="color: #a06970">:</span>
              <div class="assessment-info-text" v-html="assess.achievement"></div>
            </div>
            <div>
              <span style="color: #a06970">:</span>
              <div class="assessment-info-text" v-html="assess.chance"></div>
            </div>
          </div>
        </div>
      </div>
      <div v-if="isPage" class="pad20 bg_ff assessmentItem">
        <Page @on-change="onPageChange" :total="page.total" :current="page.current" :page-size="page.pageSize" prev-text="" next-text="" />
      </div>
    </div>
  </div>
</template>

<script>
import {
  getAssessmentByListId,
  saveAssessment
} from "@/services/appService.js";
import vueWangeditor from 'vue-wangeditor';
import EditTable from "@/components/table/edit-table";
import AssessModal from "@/components/modal/Modal";

export default {
  name: "admintrstorAssessment",
  components: {
    AssessModal,
    EditTable,
    vueWangeditor
  },
  props: {
    isAdmin: {
      type: Boolean
    }
  },
  data() {
    return {
      listId: this.$route.params.listId,
      showAssessModal: false,
      isAdminTrue: false,
      isPage: true,
      isEdit: "",
      IsEditId: "",
      changeResult: "",
      page: {
        total: 0,
        current: 1,
        pageSize: 10
      },
      adminAssessData: {
        duringDate: "",
        result: "",
        opportunity: ""
      },
      ruleValidate: {
        duringDate: [
          {
            type: "date",
            required: true,
            message: ""
          }
        ],
        result: [
          {
            required: true,
            message: ""
          }
        ],
        opportunity: [
          {
            required: true,
            message: ""
          }
        ]
      },
      assessments: [],
      menu: [
        'source',	// 
        '|',
        'bold',	// 
        'underline',	// 
        'italic',	// 
        'strikethrough',	// 
        'eraser',	// 
        'forecolor',	// 
        'bgcolor',	// 
        '|',
        'quote',	// 
        'fontfamily',	// 
        'fontsize',	// 
        'head',	// 
        'unorderlist',	// 
        'orderlist',	// 
        'alignleft',	// 
        'aligncenter',	// 
        'alignright',	// 
        '|',
        'emotion',	// 
        '|',
        'undo',	// 
        'redo'	// 
      ]
    };
  },
  watch: {
    isAdmin: function(value) {
      value && (this.isAdminTrue = true);
    }
  },
  methods: {
    //
    addAssess() {
      this.showAssessModal = true;
      this.$refs["formValidate"].resetFields();
    },
    //
    submitAdminAssess() {
      if(this.$refs.result.getText() === ''){
        this.$Message.error('');
      }else if(this.$refs.oppor.getText() === ''){
        this.$Message.error('');
      }else{
        this.adminAssessData.opportunity = document.getElementById('editorOppor').innerHTML;
        this.adminAssessData.result = document.getElementById('editorResult').innerHTML;
      }
      let params = {
        listId: this.listId,
        opportunity: this.adminAssessData.opportunity,
        result: this.adminAssessData.result,
        date: this.formatDate(this.adminAssessData.duringDate)
      };
      if (this.isEdit === "edit") {
        params.id = this.IsEditId;
      }
      let v = false;
      this.$refs["formValidate"].validate(valid => {
        v = valid;
      });
      if (v) {
        saveAssessment(params).then(res => {
          if (res.success) {
            this.$Message.success(res.message);
            this.getAssessmentData();
            this.showAssessModal = false;
            this.$refs["formValidate"].resetFields();
            document.getElementById('editorOppor').innerHTML = '';
            document.getElementById('editorResult').innerHTML = '';
            this.isEdit = "";
          } else {
            this.$Message.error(res.message);
          }
        });
      }
    },
    //
    getAssessmentData(currentPage) {
      getAssessmentByListId(this.listId,this.page.pageSize,currentPage).then(res => {
        this.assessments = res.tableContent;
        this.page.total = res.dataCount;
        if(res.tableContent.length === 0){
          this.isPage = false;
        }else{
          this.isPage = true;
        }
      });
    },
    //
    onPageChange(currentPage) {
      this.getAssessmentData(currentPage);
    },
    //
    formatDate(currentDate) {
      let date = new Date(currentDate),
        year = date.getFullYear(),
        month = date.getMonth() + 1,
        relDate;
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      relDate = year + "-" + month;

      return relDate;
    }
  },
  created() {
    this.getAssessmentData(this.page.current);
  }
};
</script>




