<style lang="less" >
.timeline-box {
  position: relative;
  margin-top: 15px;
  &-header {
    height: 48px;
    line-height: 48px;
    padding-left: 15px;
    border-bottom: 1px solid #e8e8e8;
    > span {
      font-size: 16px;
      font-weight: bold;
    }

    a {
      margin-left: 10px;
      font-size: 14px;
    }
  }

  &-form {
    height: 100%;
    background-color: #ffffff;
    padding: 15px;

    //form
    .ivu-form-item-label {
      font-size: 14px;
    }

    &-submit {
      background-color: rgb(0, 150, 136);
      color: #fff;
      outline: none;
      font-weight: bold;
      cursor: pointer;
      border: 1px solid transparent;
      padding: 6px 15px;
      line-height: 1.5;
      float: right;
    }
  }

  &-log {
    &-item {
      margin-top: 2px;
      padding: 15px 20px;
      background-color: #ffffff;

      .timeline-item-content-header {
        .circle {
          border: 1px solid #2c9383;
          height: 14px;
          width: 14px;
          border-radius: 50%;
          display: inline-block;
        }

        .time {
          font-size: 20px;
          font-weight: 400;
          margin-left: 6px;
        }
      }

      .timeline-item-content-ul {
        margin-left: 40px;
        font-size: 13px;
        list-style: disc;
        span {
          padding: 0 20px 0 0;
        }

        .customs-tag {
          margin: 0 8px;
          background-color: #2c9383;
          padding: 2px 8px;
          border: 1px solid #2c9383;
          border-radius: 3px;
          color: #ffffff;
          font-size: 13px;
          vertical-align: middle;
          opacity: 1;
          overflow: hidden;
          cursor: pointer;
        }
      }
    }
  }

  .loading-more {
    line-height: 30px;
    font-size: 14px;
    text-align: center;
    color: darkgray;
    height: 30px;
  }
}

.ivu-timeline-item-content {
  top: -11px !important;
}
</style>

<template>
  <div fix class="timeline-box">
    <div class="app-resource-group-title">
      <h3></h3>
    </div>

    <div class="timeline-box-form" v-if="isAdminTrue">
      <Form ref="formValidate" :label-width="120" :model="modalFormData" :rules="ruleValidate">
        <FormItem label=":" prop="scope" width="300">
          <Select multiple v-model="modalFormData.scope">
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
          </Select>
        </FormItem>
        <FormItem label=":" prop="spendTime">
          <InputNumber v-model="modalFormData.spendTime" />
          <span style="margin-left:10px;">/</span>
        </FormItem>
        <FormItem label=":" prop="content">
          <vue-wangeditor ref="editor" id="editor" v-model="modalFormData.content" :menus="menu" height="143" width="100%"></vue-wangeditor>
        </FormItem>
        <FormItem>
          <input type='submit' value="" class="timeline-box-form-submit" @click="submitLog" />
        </FormItem>
      </Form>
    </div>

    <div v-show="logData.length===0?false:true">
      <div class="timeline-box-log-item" v-for="(item,index) in logData" :key="index">
        <div class="timeline-item-content-header">
          <div class="circle"></div>
          <span class="time">{{item.VERSION}}</span>
        </div>
        <ul class="timeline-item-content-ul">
          <li>
            <span>{{item.CREATOR_NAME}}</span>
            <span>{{item.CRT_TIME}}</span>
            <span>:{{item.TIME_CONSUMING}}</span>
          </li>
          <li>
            <span>:</span>
            <span class="customs-tag" v-for="(scope,index) in item.CHANGE_RANGE" :key="index">{{scope}}</span>
          </li>
          <li>
            :
            <span v-html="item.CONTENT"></span>
          </li>
        </ul>
      </div>
      <div class="loading-more" v-if="currentPage*10<dataCount">
        <a @click="handleLoadingMore">
          <i class="iconfont">&#xe63d;</i>
          
        </a>
      </div>
      <div v-else class="loading-more">
          ...
      </div>
    </div>
  </div>
</template>

<script>
import { getChangeLog, saveAppLog } from "@/services/appService.js";
import vueWangeditor from "vue-wangeditor";

export default {
  name: "ChangeLog",
  components: {
    vueWangeditor
  },
  props: {
    listId: {
      type: String
    },
    isAdmin: {
      type: Boolean
    }
  },
  data() {
    return {
      isAdminTrue: false,
      logData: [],
      modalFormData: {
        //
        scope: [],
        spendTime: 1,
        content: ""
      },
      ruleValidate: {
        //
        scope: [
          {
            type: "array",
            required: true,
            message: ""
          }
        ],
        spendTime: [
          {
            required: true,
            message: "",
            type: "number"
          }
        ],
        content: [
          {
            required: true,
            message: ""
          }
        ]
      },
      menu: [
        "source", // 
        "|",
        "bold", // 
        "underline", // 
        "italic", // 
        "strikethrough", // 
        "eraser", // 
        "forecolor", // 
        "bgcolor", // 
        "|",
        "quote", // 
        "fontfamily", // 
        "fontsize", // 
        "head", // 
        "unorderlist", // 
        "orderlist", // 
        "alignleft", // 
        "aligncenter", // 
        "alignright", // 
        "|",
        "emotion", // 
        "|",
        "undo", // 
        "redo" // 
      ],

      dataCount:0,
      currentPage: 1
    };
  },

  watch: {
    isAdmin: function(value) {
      if (value) {
        this.isAdminTrue = true;
      } else {
        this.isAdminTrue = false;
      }
    }
  },

  methods: {
    modalVisibleChange(val) {
      this.$refs["formValidate"].resetFields();
    },

    /**
     * 
     */
    submitLog(event) {
      //
      let valid;
      if (this.$refs.editor.getText() === "") {
        this.$Message.error("");
      } else {
        this.modalFormData.content = document.getElementById(
          "editor"
        ).innerHTML;
      }

      this.$refs["formValidate"].validate(v => {
        valid = v;
      });
      if (valid) {
        let listId = this.listId,
          scope = this.modalFormData.scope.join(","),
          spendTime = this.modalFormData.spendTime,
          content = this.modalFormData.content;

        saveAppLog(listId, scope, spendTime, content).then(res => {
          if (res.success) {
            this.$Message.success(res.message);
            document.getElementById("editor").innerHTML = "";
            this.modalFormData.scope = [];
            this.getChangeLog();
          } else {
            //faild todo
          }
        });
      }
    }, 

    /**
     * 
     */
    getChangeLog() {
      let listId = this.listId;

      getChangeLog(listId).then(res => {
        if (res.tableContent) {
          res.tableContent.map(item => {
            if (item["CHANGE_RANGE"]) {
              item["CHANGE_RANGE"] = item["CHANGE_RANGE"].split(",");
            }
          });
          this.logData = res.tableContent;
          this.dataCount = res.dataCount;
        }
      });
    },

    handleLoadingMore(event) {
      this.currentPage++;
      getChangeLog(this.listId, this.currentPage).then(res => {
        if (res.tableContent) {
          res.tableContent.map(item => {
            if (item["CHANGE_RANGE"]) {
              item["CHANGE_RANGE"] = item["CHANGE_RANGE"].split(",");
            }
            this.logData.push(item);
          });
        }
      });
    },
  
  },

  created() {
    this.getChangeLog();
  },
};
</script>




